	module.exports = {"data":{"post":{"html":"<p>I won't be at the Whiney Expat Meetup in November. MamaMaus needs me to accompany her to her firm's annual charity auction, this time in Munich. I'll again need to rent a penguin suit. The next day we've been invited to a 100th birthday party. The proprietors of the <a href=\"http://www.naturheilzentrumdrochtersen.de/\">Naturheilzentrum Drochtersen</a> (there's a <a href=\"http://www.papascott.de/archives/2006/10/03/google-patriotism/#comment-21025\">free link for you Kirsten</a> :-)) and their daughter have their birthdays on the same day, and their ages add up to 100. Ten years ago they celebrated their 75th birthday(s), it's amazing how the time flies. (Math puzzle: how old is their daughter this year?)</p>\n<p>Meanwhile, the two-week fall school vacation starts, um, now. MamaMaus has a 4-day engagement in Lisbon this week, so we men are going to sneak away as well... for 3 days in Denmark. We didn't see all of Legoland the first time we were there, so Christopher and I have to check out all the things we missed.</p>\n<p>Tomorrow we're taking a day-long joyride to Hannover. Mama has business there, and since her Audi doesn't have enough horsepower for Christopher, we'll take the high-speed ICE train, even though it takes longer. It's not hanging around bored in train stations waiting for our connection, it's conducting observational studies of consumer traffic patterns for retail space in a metropolitan transit center. Oh joy!</p>\n<p>Our big plan for fall vacation for Christopher though is swimming lessons. He's hoping to earn his <a href=\"http://de.wikipedia.org/wiki/Fr%C3%BChschwimmer#Deutschland_.28.22Seepferdchen.22.29\"><em>Seepferdchen</em></a> (little sea horse) patch by swimming 25 meters. Well, we're hoping for that anyway.</p>","fields":{"title":"Travel Plans","date":"13 Oct 2006","url":"https://papascott.de/2006-10-13-travel-plans/"}}},"pathContext":{"slug":"/2006-10-13-travel-plans/","prev":{"fields":{"title":"Internet Killed the Video Star","date":"09 Oct 2006","slug":"/2006-10-09-internet-killed-the-video-star/"}},"next":{"fields":{"title":"Make Money At Home With Blogging","date":"16 Oct 2006","slug":"/2006-10-16-make-money-at-home-with-blogging/"}}}}

